import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/vinos',
    name: 'vinos',
    component: () => import('../views/Vinos.vue')
  },
  {
    path: '/promocion',
    name: 'promocion',
    component: () => import('../views/Promocion.vue')
  },
  {
    path: '/accesorios',
    name: 'accesorios',
    component: () => import('../views/Accesorios.vue')
  },
  {
    path: '/checkout',
    name: 'checkout',
    component: () => import('../views/CheckOut.vue')
  },
  {
    path: '/confirmacion',
    name: 'confirmacion',
    component: () => import('../views/Confirmacion.vue')
  },
  {
    path: '/:pathMatch(.*)*',
    name: 'notfound',
    component: () => import('../views/NotFoundVue.vue')
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
